<script>
import DataService from "@/services/DataService";
export default {
    components: {

    },
    data() {
        return {
            isLoading: false,
            PostId: 0,
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,
            TotalRecords: 0,
            Headers: null,
            CandidateData: [],
            CandidateDetail: [],
            documentList: [],
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false,
            ApplicantId: '',
            DepartmentId: 0,
            searchCondition: '',
            SkillIds: 0,
            CandidateData: [],
            candidate_profile_path: "~/Content/images/DefaultImages/avatar-icon.jpg",
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.PostId = this.$route.params.pid;
        vm.SkillIds = this.$route.params.sid;
        await vm.FetchData();
    },
    computed: {
        sortedCategories() {
            const uniqueValues = [...new Set(this.CandidateData.map(item => item.first_name))];
            const distinctValues = uniqueValues.map(firstName => {
                return this.CandidateData.find(item => item.first_name === firstName);
            });
            const sortedValues = distinctValues.map((value, index) => ({
                value,
                rowNumber: index
            }));
            sortedValues.sort((a, b) => a.rowNumber - b.rowNumber);
            return sortedValues.map(item => item.value)
        }
    },
    methods: {
        Close: function () {
            this.$router.push({
                name: 'JobList'
            });
        },
        AddAsCandidate: function () {
            var vm = this;
            vm.$router.push({
                name: 'HRAddAsCandidate',
                params: {
                    id: vm.PostId
                }
            });
        },
        JobDeatailRedirection: function (value) {
            var vm = this;
            vm.$router.push({
                name: 'JobPost',
                params: {
                    id: value,
                    type: 'JobPostDetail'
                }
            });
        },
        DownloadResume: async function (path, name) {
            var vm = this;
            var fileName = name;
            var filePath = path;
            if (filePath == "" && fileName == "" || filePath == null) {
                vm.ShowAlert(vm.$t("NoAttachment"), "warning", true, vm.$t("Alert"));
            } else {
                var url = `filePath=${filePath}&fileName=${fileName}`;
                await DataService.DownloadFile(url).then(async function (response) {
                    const byteData = response.data;
                    const fileURL = `data:image/png;base64;base64,${byteData}`;
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })
            }
        },
        applicantDetails: function (val, post_id) {
            var vm = this;
            vm.Documents(val);
            vm.CandidateData.forEach(function (data) {
                if (data.applicant_id == val && data.post_id == post_id) {
                    vm.CandidateDetail = data;
                }
            })
        },
        Documents: async function (id) {
            var vm = this;
            var url = `applicantId=${id}&langCode=EN`;
            await DataService.GetDocumentsList(url).then(function (response) {
                if (response.data != null) {
                    vm.Headers = response.data.schema;
                    if (response.data != "" && response.data.data.length > 0) {
                        vm.documentList = response.data.data;
                    } else {
                        vm.documentList = [];
                    }
                } else {
                    vm.documentList = [];
                }
            })
        },
        FetchData: async function () {
            var vm = this;
            var url = `postId=${vm.PostId}&applicantId=${vm.ApplicantId}&skillIds=${vm.SkillIds}&departmentId=${vm.DepartmentId}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&searchCondition=${vm.searchCondition}`;
            await DataService.TalentPoolList(url).then(function (response) {
                if (response.data != '') {
                    vm.CandidateData = response.data.data;
                    vm.CandidateDetail = response.data.data[0];
                    vm.TotalRecords = response.data.data[0].total_records;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecord = false;
                } else {
                    vm.noRecord = true;
                    vm.CandidateData = [];
                    vm.TotalRecords = 0;
                    vm.ShowAlert(vm.$t("No_Record_Found"), "warning", true, vm.$t("Alert"));
                }
            });
        }
    }
}
</script>
